<template>
    <section>
        <div class="col-12 listing tblassestgroupinfo">
            <div class="table-responsive">
                <table id="tblassestgroupinfo" class="table table-bordered dt-responsive nowrap mt-0">
                    <thead class="thead-bg">
                        <tr>
                            <th class="text-center">
                                <span>{{ $t('SerialNumber') }}</span>
                            </th>
                            <th class="text-center"><span>{{ $t('ItemName') }}</span></th>
                            <th class="text-center"><span>{{ $t('PairProduct') }}</span>
                            </th>
                            <th class="text-center"><span>{{ $t('ItemMandatoryForCheckOut') }}</span>
                            </th>
                            <th class="text-center">
                                <span>{{ $t('Action') }}</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody v-for="(item, index) in ProductData" :key="index">
                        <tr>
                            <td class="text-center">
                                {{ index + 1 }}
                            </td>
                            <td>{{ item.Name }}</td>

                            <td v-if="iselectVal == null" class="text-center">

                                <select class="form-control" :class="{
                                    'is-invalid': item.isValidation ? false : true

                                }" v-on:change="bindProductItem($event, item.AssetCatalogueId)">
                                    <option value="">Select</option>
                                    <option v-for="(item, itemIndex) in AssetTypeData" :key="itemIndex" :value="item.Value">
                                        {{ item.Text }}
                                    </option>
                                </select>


                            </td>


                            <td v-else class="text-center">

                                <select class="form-control" :class="{
                                    'is-invalid': item.isValidation ? false : true

                                }" v-on:change="bindProductItem($event, item.AssetCatalogueId)">
                                    <option value="">Select</option>
                                    {{ UniqueItemData }}
                                    <option :value="i.RELATED_INFO_ID" v-for="(i, itemIndex) in UniqueItemData"
                                        :key="itemIndex">{{ i.UNIQUE_NAME }}
                                    </option>
                                </select>

                            </td>


                            <td class="text-center">
                                <div class="form-group">
                                    <label class="switch">
                                        <input id="isCheck" type="checkbox" v-model="item.IsMandatory"
                                            v-on:change="ChangeCheck($event)"
                                            class="form-check-input rdoticketfor custom-control-input dynamic" />
                                        <span class="slider round">
                                            <span class="slider-yes">Yes</span>
                                            <span class="slider-no">No</span>
                                        </span>
                                    </label>
                                </div>
                            </td>

                            <td class="text-center single-action">

                                <a v-if="parseInt(result) == parseInt(index + 1)" href="javascript:;" id="addProduct"
                                    title="Add Product" @click="AddMoreItem(item);"><i
                                        class="fa fa-plus px-4 text-success"></i></a>

                                <a href="javascript:;" class="btnDelete" title="Delete"
                                    @click="deleteItem(item.AssetCatalogueId)"><i
                                        class="fa fa-trash px-4 text-danger"></i></a>
                            </td>
                        </tr>
                        <tr>
                            <td v-if="NoRecordLength == 0" colspan="7" class="text-danger text-center no-record">No Record
                                Found</td>
                        </tr>

                    </tbody>
                </table>
            </div>
            <div class="col-12 text-right mt-3">
                <a v-on:click="SubmitData()" href="javascript:;" title="Save" class="btn btn-success"><i
                        class="fa fa-save pr-2"></i>{{ $t('Save') }} </a>&nbsp;
                <a href="javascript:;" title="Cancel" onclick=" $('#divDialogAddRelationship').dialog('close');"
                    id="ancCancelgroupRequisition" class="btn btn-danger"><i class="fa fa-close pr-2"></i>{{ $t('Cancel')
                    }}</a>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
export default {
    props: {

        UniqueItemData: {
            type: Array
        },
        ProductData: {
            type: Array
        },
        LocationValue: {
            type: Number
        },
        txtAssetType: {
            type: Number
        },
        iselectVal: {
            type: Number
        },
        ClosePopup: {
            type: Function
        },
        LocationValueForBarCode: {
            type: Number,
        },
        txtBarcode: {
            type: String
        },
        FetchData: {
            type: Function
        },
        AssetTypeData: {
            type: Array
        }
    },


    data() {

        return {
            NoRecordLength: null,
            noDataAvailable: 'No Product is available in the items list.',
            isCheckDeployItem: '',
            product: {},
            Relations: [],
            sliderValue: 0,
            IsMandatory: false,
            ChildId: null,
            result: 1,
            isVal: false
        }
    },



    created() {

        debugger;
        console.log("productName", this.txtAssetType);
        console.log("Product", this.iselectVal);

    },



    updated() {

        this.lastItem();

    },

    methods: {

        lastItem() {
            this.result = this.ProductData.length;
        },

        ChangeCheck: function (event) {

            var vm = this;
            vm.IsMandatory = false;
            if (event.target.checked == true) {

                if (vm.ChildId != null) {

                    vm.Relations.forEach(element => {
                        var itemExist = vm.Relations.some(x => x.ChildId === vm.ChildId)
                        if (itemExist == true) {
                            element.IsMandatory = true;
                        }
                        else {
                            element.IsMandatory = false;
                        }
                    })

                    vm.IsMandatory = false;
                }

                return;

            }
            else {

                return vm.IsMandatory = false;
            }
        },



        AddMoreItem(item) {

            var vm = this;
            if (vm.ChildId != null) {

                const product = {
                    Name: item.Name,
                    AssetCatalogueId: item.AssetCatalogueId,

                }


                vm.ProductData.push(product);
                vm.ChildId = null;
            }

        },


        bindProductItem(value, item) {

            var vm = this;
            var temp = [];
            let isChildExit = false;
            vm.ChildId = parseInt(value.target.value);
            if (vm.Relations.length == 0) {
                let childId = { ChildId: vm.ChildId, IsMandatory: false }
                vm.Relations.unshift(childId);

                this.ProductData.forEach(ele => {

                    var index = vm.ProductData.find(x => x.AssetCatalogueId == item)
                    if (index != null) {
                        ele.isValidation = true;
                    }

                })

                return

            }
            temp.push(vm.Relations);
            temp.forEach(ele => {
                isChildExit = vm.Relations.some(x => x.ChildId === parseInt(value.target.value));
                if (isChildExit == false) {
                    let childId = { ChildId: vm.ChildId, IsMandatory: false }
                    vm.Relations.push(childId);

                    this.ProductData.forEach(ele => {

                        var index = vm.ProductData.find(x => x.AssetCatalogueId == item)
                        if (index != null) {
                            ele.isValidation = true;
                        }

                    })

                    return;
                }
                else {
                    value.target.value = null;
                    vm.isVal = true;
                    return vm.ShowAlert(
                        vm.$t("Relation with this Product Has already been added."),
                        "failure",
                        vm.$t("Alert")
                    );
                }

            })

        },

        deleteItem(value) {
            var vm = this;
            const index = vm.ProductData.findIndex(x => x.AssetCatalogueId === value);
            if (index !== -1) {
                vm.ProductData.splice(index, 1);
            }
            return vm.ShowAlert(
                vm.$t("Item has been deleted Successfully "),
                "success",
                vm.$t("Alert")
            );

        },

        SubmitData: function () {

            var vm = this;
            let obj = {};

            if (vm.Relations.length == 0) {
                return vm.ShowAlert(
                    vm.$t("Product Name is required."),
                    "warning",
                    vm.$t("Alert"))
            }

            if (this.LocationValueForBarCode == "") {

                obj = {
                    "ParentId": this.txtAssetType ? parseInt(this.txtAssetType) : this.iselectVal,
                    "type": this.txtAssetType ? "ProductName" : "Product",
                    "LocationId": this.LocationValue,
                    "Relations": vm.Relations
                }
            }
            else {

                obj = {
                    "ParentId": parseInt(this.txtBarcode),
                    "type": "ProductName",
                    "LocationId": this.LocationValueForBarCode,
                    "Relations": vm.Relations
                }

            }


            DataService.SaveAssetRelationship(obj).then(response => {

                if (response.data != null) {
                    var result = response.data;
                    if (result.status == 0) {

                        this.ClosePopup();
                        debugger;

                        // vm.FetchData();


                        return vm.ShowAlert(
                            vm.$t("Add relationship Submitted successfully "),
                            `${result.msg}`,
                            vm.$t("Alert")
                        );


                    }
                    else {
                        vm.ShowAlert(
                            vm.$t("Something went wrong"),
                            `${result.msg}`,
                            vm.$t("Alert")
                        );
                    }
                }

            });
        }
    }
}
</script>