<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('AddRelationship') }}
                            </h5>
                            <button v-on:click="ClosePopup()" type="button" class="close" title="Close"><em
                                    class="fa fa-times"></em></button>
                        </div>


                        <div class="modal-body">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                <div class="row">
                                    <div class="col-12">
                                        <div class="rad_value" type="radiobutton">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                    id="SearchProductName" @change="searchByProduct('barcode')"
                                                    :checked="SearchByBarcode" name="SearchProduct" value="CheckedCheckBox">
                                                <label class="custom-control-label" for="SearchProductName">{{
                                                    $t('SearchbyBarcodeRFID') }}</label>

                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic IssuedByIds"
                                                    id="SearchProducts" @change="searchByProduct('product')"
                                                    :checked="!SearchByBarcode" name="SearchProduct"
                                                    value="CheckedCheckBox">
                                                <label class="custom-control-label" for="SearchProducts">{{
                                                    $t('SearchbyProduct') }}</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-12 float-left mt-3 SearchByBarcode p-0">
                                        <div class="col-12 col-sm-12  float-left" v-if="SearchByBarcode">
                                            <div class="form-group mb-3">
                                                <label><span id="">{{ $t('Searchby') }}
                                                        {{ $t('Barcode') }}/{{ $t('SerialNumber') }}/{{ $t('RFID')
                                                        }}:</span></label><span class="text-danger">*</span>

                                                <Field name="BarCode" class="w-100" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">
                                                    <input name="BarCode" placeholder="Enter Barcode" v-model="txtBarcode"
                                                        @keyup="searchfilter()"
                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        type="text">
                                                    <span v-if="errors[0]" name="BarCode"
                                                        class="invalid-feedback d-block">{{
                                                            errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                    </div>

                                    <div v-if="SearchByBarcode" class="col-9 float-left searchByProduct">
                                        <div class="form-group">
                                            <label for="Subject">Location:<span class="text-danger">*</span></label>

                                            <select name="Location" v-model="LocationValueForBarCode"
                                                v-bind:class="{ 'form-control': true }">
                                                <option value="" :disabled="LocationValueForBarCode">{{ $t('Select') }}
                                                </option>
                                                <option :disabled="LocationValueForBarCode"
                                                    v-if="LocationValueForBarCode != ''" :value="LocationValueForBarCode">
                                                    {{ LocationName }}
                                                </option>
                                            </select>

                                        </div>
                                    </div>

                                    <div v-if="!SearchByBarcode" class="col-12 float-left searchByProduct">
                                        <div class="form-group">
                                            <!-- 
                                            <select v-if="isdata" name="Location" v-model="LocationValue"
                                                v-on:change="BindItemTypeByUserId(LocationValue)"
                                                v-bind:class="{ 'form-control': true }">
                                                <option value="">{{ $t('Select') }}</option>
                                                <option :value="item.COMPANY_LOCATION_ID" v-for="item in LocationData"
                                                    :key="item">
                                                    {{ item.LOCATION_NAME }} </option>
                                            </select> -->

                                            <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                            <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                                v-slot="{ errors }">
                                                <tree-view v-model="LocationValue" :options="options"
                                                    :select="BindItemTypeByUserId(LocationValue)"
                                                    placeholder="Select Location" :show-count="true"
                                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                </tree-view>
                                                <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                                    errors[0]
                                                }}</span>

                                            </Field>

                                        </div>
                                    </div>



                                    <div class="col-sm-6 col-md-6 col-lg-2 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4"
                                        v-if="SearchByBarcode">
                                        <div class="background-0 text-left w-100 form-check-inline">
                                            <input type="hidden" id="hdnSearch" value="">
                                            <a title="" id="Searchassest_barcode" href="javascript:;"
                                                v-on:click="SearchAssetByBarCode()"
                                                class="btn btn-success w-50 mr-1 px-4 hlsearch"
                                                data-toggle-tooltip="tooltip" data-original-title="Search"><em
                                                    class="fa fa-search"></em></a>
                                            <a id="aClear" href="javascript:;" title="Clear all "
                                                v-on:click="ClearhdnIdsOnChange()"
                                                class="btn btn-danger w-50 clearsearchtext px-4"
                                                data-toggle-tooltip="tooltip" data-original-title="Clear"><em
                                                    class="fa fa-refresh"></em></a>
                                            <!-- 
                                            <a href="javascript:;" class="pullleft mx-1 green btn btn-info w-33"
                                                title="Click here to search"><i
                                                    class="fa fa-barcode text-white float-left mt-1 mr-2"></i><span
                                                    class="text-white">RFID Scanned</span></a> -->
                                        </div>


                                    </div>


                                    <div id="searchByProduct" v-if="!SearchByBarcode" class="col-12 float-left">

                                        <div class="col-12 float-left p-2">
                                            <div class="rad_value" type="radiobutton">
                                                <div class="custom-control custom-radio custom-control-inline">
                                                    <input type="radio" class="custom-control-input dynamic"
                                                        id="ProductNameItem" @change=" searchByItemName('ProductName')"
                                                        :checked="IsProductName" value="ProductName">
                                                    <label class="custom-control-label" for="ProductNameItem">{{
                                                        $t('AssetId') }}</label>
                                                </div>
                                                <div class="custom-control custom-radio custom-control-inline">
                                                    <input type="radio" class="custom-control-input dynamic" id="ItemName"
                                                        @change="searchByItemName('Product')" :checked="!IsProductName"
                                                        value="Product">
                                                    <label class="custom-control-label" for="ItemName">{{ $t('ItemName')
                                                    }}</label>
                                                </div>
                                            </div>
                                        </div>
                                        <br />
                                        <div class="col-9 float-left" v-if="IsProductName">
                                            <div class="form-group">
                                                <label>{{ $t('ProductName') }}:<span class="text-danger">*</span></label>
                                                <Field name="Product" class="w-100" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">
                                                    <select name="AssetTypeData" v-model="txtAssetType"
                                                        v-bind:class="{ 'form-control': true }">
                                                        <option value="">Select</option>
                                                        <option v-for="(item, itemIndex) in AssetTypeData" :key="itemIndex"
                                                            :value="item.Value">{{ item.Text }}
                                                        </option>
                                                    </select>
                                                    <span v-if="errors[0]" class="invalid-feedback d-block">{{
                                                        errors[0] }}</span>
                                                </Field>
                                            </div>

                                        </div>


                                        <div class="col-9 float-left" v-if="!IsProductName">
                                            <div class="form-group">
                                                <label>Item Name:<span class="text-danger">*</span></label>
                                                <div class="input-group mb-3" style="position: relative;">
                                                    <input id="txtAssetItems" name="txtAssetItems" v-model="assetUniqueItem"
                                                        placeholder="Enter Product Name" class="form-control" type="text"
                                                        autocomplete="off">
                                                    <div class="unique_dynamicdatalist">
                                                        <ul class="listOfProduct" v-if="IsDDLShowHide">
                                                            <li class="listitem all-items"
                                                                v-for="(item, index) in UniqueItemData"
                                                                v-on:click="uniqueItemChange(item)"
                                                                :value="item.RELATED_INFO_ID" :key="index">
                                                                {{ item.UNIQUE_NAME }}</li>
                                                        </ul>
                                                    </div>
                                                    <a class="btn float-left" href="javascript:;"
                                                        v-on:click="itemDDLShowHide"
                                                        style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"
                                                        id="customddlinput">
                                                        <em class="dropdown-toggle float-right"></em>
                                                    </a>
                                                </div>

                                            </div>
                                        </div>


                                        <div class="col-sm-8 col-md-8 col-lg-2 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4">
                                            <div class="background-0 text-left w-100 form-check-inline">
                                                <input type="hidden" id="hdnSearch" value="">
                                                <a title="" @click="SearchAssetByProduct" href="javascript:;"
                                                    class="btn btn-success w-50 mr-1 px-4 hlsearch"
                                                    data-toggle-tooltip="tooltip" data-original-title="Search"><em
                                                        class="fa fa-search"></em></a>
                                                <a id="aClear" href="javascript:;" title="Clear all "
                                                    v-on:click="ClearhdnIdsOnChange()"
                                                    class="btn btn-danger w-50 clearsearchtext px-4"
                                                    data-toggle-tooltip="tooltip" data-original-title="Clear"><em
                                                        class="fa fa-refresh"></em></a>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                                <AddrelationListing :ProductData="ProductData" :UniqueItemData="UniqueItemData"
                                    :LocationValueForBarCode="LocationValueForBarCode" :iselectVal="iselectVal"
                                    :txtBarcode="txtBarcode" :txtAssetType="txtAssetType" :LocationValue="LocationValue"
                                    :AssetTypeData="AssetTypeData" :FetchData="FetchData" :ClosePopup="ClosePopup"
                                    v-if="searchResult">
                                </AddrelationListing>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>


<script>
import DataService from '../../service/DataService';
import AddrelationListing from '../../components/Modal/AddRelationListing.vue';
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        AddrelationListing, 'tree-view': Treeselect
    },
    props: {
        FetchData: {
            type: Function
        }
    },
    data() {
        return {
            SearchByBarcode: true,
            IsProductName: true,
            SearchByProduct: false,
            LocationData: [],
            LocationValue: '',
            LocationValueForBarCode: null,
            AssetTypeData: [],
            txtBarcode: null,
            txtAssetType: '',
            searchResult: false,
            isLoading: false,
            IsDDLShowHide: false,
            assetUniqueItem: '',
            iselectVal: null,
            UniqueItemData: [],
            LocationName: '',
            ProductData: [],
            options: []

        }
    },
    created: function () {
        var vm = this;
        vm.searchByProduct('barcode');
        vm.GetLocationByCompanyId()

    },
    methods: {


        searchByProduct: function (val) {
            var vm = this;
            this.SearchByBarcode = false;
            if (val == 'barcode') {
                vm.isdata = false;
                vm.SearchByBarcode = true;
                vm.CheckedCheckBox = 'SearchProductName';
                vm.BarcodeSelected = 'BarCodeSearch';
                vm.ProductData = [];
            } else {
                vm.isdata = true;
                vm.SearchByBarcode = false;
                vm.CheckedCheckBox = 'SearchProducts';
                vm.BarcodeSelected = 'ProductSearch';
                vm.ProductData = [];
            }
            this.ClearhdnIdsOnChange();
        },

        ClearhdnIdsOnChange: function () {
            var vm = this;
            vm.txtBarcode = '';
            vm.LocationValueForBarCode = '',
                vm.LocationValue = '',
                vm.assetUniqueItem = '';

        },

        itemDDLShowHide: function () {

            this.IsDDLShowHide = !this.IsDDLShowHide;
        },

        uniqueItemChange: function (item) {
            debugger;
            this.iselectVal = item.RELATED_INFO_ID;
            this.assetUniqueItem = item.UNIQUE_NAME;
            this.IsDDLShowHide = false;
        },


        searchByItemName: function (val) {

            this.IsProductName = false;
            if (val == 'ProductName') {
                this.IsProductName = true;
                this.IsProduct = 'ProductName';
                this.assetUniqueItem = '';
            } else {
                this.IsProductName = false;
                this.IsProduct = 'Product';
            }
        },

        BindItemTypeByUserId: function (ths) {
            debugger;
            var vm = this;
            var url = `id=&isView=&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.LocationValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                vm.AssetTypeData = response.data.Result;
            });

            vm.GetAssetUniqueData();

        },

        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=assetassignment`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },



        ClosePopup() {
            this.$parent.CloseAddRelationShipPopup();
        },


        searchfilter: function () {

            var vm = this;
            var url = `id=&isView=1&barcode=${this.txtBarcode}&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                if (response.data[0] != null) {
                    debugger;
                    this.LocationName = response.data[0].LocationName;
                    this.LocationValueForBarCode = response.data[0].LocationId;
                }
            })

        },


        

        SearchAssetByBarCode() {
            var vm = this;
            var url = `id=&isView=1&barcode=${this.txtBarcode}&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=yes&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.LocationValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                if (response.data.length > 0) {
                    vm.searchResult = true;
                    vm.ProductData = response.data;
                    let isValidation = false
                    vm.ProductData.map(ele => {
                        ele.isValidation = isValidation;
                    })
                }
                else {
                    vm.searchResult = true;
                    vm.NoRecordLength = 0;
                }

            })
            vm.GetAssetUniqueData();
        },

        GetAssetUniqueData: function () {

            var vm = this;
            let url = '';
            vm.isLoading = true;

            if (vm.LocationValueForBarCode == null || vm.LocationValueForBarCode == '') {
                url = `assetCatalogId=&name=&issueTo=&locationId=${vm.LocationValue}&requestFrom=addAssetRelationShip&assetType=`;
            }
            else {
                url = `assetCatalogId=&name=&issueTo=&locationId=${vm.LocationValueForBarCode}&requestFrom=addAssetRelationShip&assetType=`;
            }
            DataService.GetUniqueAssetItems(url).then(function (response) {
                vm.UniqueItemData = response.data;
            })
            vm.isLoading = false;

        },

        SearchAssetByProduct: function () {

            let url = '';
            var vm = this;

            if (vm.iselectVal == null) {
                url = `id=${vm.txtAssetType}&isView=1&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=no&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.LocationValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=${vm.txtAssetType}`;
            }
            else {
                url = `id=&isView=1&barcode=&assetType=0&assetUniqueId=${vm.iselectVal}&selfCheckout=&reqFrom=addAssetRelationShip&isFor=search&isuniqueSearch=no&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.LocationValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${vm.iselectVal}&catalogueIds=`;

            }
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                if (response.data.length > 0) {
                    vm.searchResult = true;
                    vm.ProductData = response.data;
                    let isValidation = false
                    vm.ProductData.map(ele => {
                        ele.isValidation = isValidation;
                    })
                }
                else {
                    vm.searchResult = true;
                    vm.NoRecordLength = 0;
                }
            })

        }
    }
}
</script>